.card {
	border: 1px solid #f3f3f3;
	width: 220px;
	padding: 30px;
	border-radius: 40px;
	margin-right: 30px;
	margin-bottom: 30px;
	transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

	.plus {
		cursor: pointer;
	}
	&:hover {
		box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.07);
		transform: translateY(-5px);
	}

	h5 {
		font-weight: 400;
		font-size: 14px;
	}
	span {
		text-transform: uppercase;
		font-size: 13px;
		opacity: 0.5;
		b {
			font-size: 12px;
		}
	}
}
